<template>
  <div>
    <div class="personal-info-section">
      <h2 class="text-3xl mb-2">A Little about you</h2>
      <p>Please fill some of the basic details to get started</p>
      <div class="grid gap-6 grid-cols-2 my-8">
        <div>
          <Input
            v-model="formData.first_name"
            :view="(validation.first_name.$invalid && 'danger') || 'default'"
            class="xl:col-span-1 lg:col-span-1 md:col-span-1 col-span-2"
            placeholder="First Name"
            block
          />
          <span
            class="text-red-base mt-2 block"
            v-for="(error, errorIndex) of validation.first_name.$errors"
            :key="errorIndex"
            >{{ error.$message }}.
          </span>
        </div>
        <div>
          <Input
            v-model="formData.last_name"
            :view="(validation.last_name.$invalid && 'danger') || 'default'"
            class="xl:col-span-1 lg:col-span-1 md:col-span-1 col-span-2"
            placeholder="Last Name"
            block
          />
          <span
            class="text-red-base mt-2 block"
            v-for="(error, errorIndex) of validation.last_name.$errors"
            :key="errorIndex"
            >{{ error.$message }}.
          </span>
        </div>
        <div class="col-span-2">
          <Input
            v-model="formData.email"
            :view="(validation.email.$invalid && 'danger') || 'default'"
            class="col-span-2 mb-6"
            placeholder="Business Email Address"
            autocomplete="username"
            block
          />
          <span
            class="text-red-base mt-2 block"
            v-for="(error, errorIndex) of validation.email.$errors"
            :key="errorIndex"
            >{{ error.$message }}.
          </span>
        </div>
        <div class="col-span-2 w-full">
          <InputPhone v-model="formData.phone_number" block />
          <span
            class="text-red-base mt-2 block"
            v-for="(error, errorIndex) of validation.phone_number.$errors"
            :key="errorIndex"
            >{{ error.$message }}.
          </span>
        </div>
        <div>
          <SelectCountry
            v-loading="loading.countries"
            @select="handleChangeCountry"
            class="xl:col-span-1 lg:col-span-1 md:col-span-1 col-span-2"
            :countries="countries"
          />
          <span
            class="text-red-base mt-2 block"
            v-for="(error, errorIndex) of validation.country_code.$errors"
            :key="errorIndex"
            >{{ error.$message }}.
          </span>
        </div>
        <div
          class="col-span-2 xl:col-span-1 lg:col-span-1 md:col-span-1 w-full"
        >
          <SelectCurrency
            v-loading="loading.currencies"
            @change="handleChangeCurrency"
            :currencies="currencies"
          />
          <span
            class="text-red-base mt-2 block"
            v-for="(error, errorIndex) of validation.currency.$errors"
            :key="errorIndex"
            >{{ error.$message }}.
          </span>
        </div>
        <div class="col-span-2">
          <Select
            v-model="formData.location_type"
            name="name"
            by="value"
            :items="locationTypes"
            placeholder="Choose your included location type"
            class="w-full"
            block
          />
          <span class="text-grey-03 block my-2 text-sm"
            >You can add additional location types later</span
          >
          <span
            class="text-red-base mt-2 block"
            v-for="(error, errorIndex) of validation.location_type.$errors"
            :key="errorIndex"
            >{{ error.$message }}.
          </span>
        </div>
      </div>
    </div>
    <div class="password-section">
      <h2 class="text-3xl mb-2">Enter your password</h2>
      <p>Please make your password as strong as possible</p>
      <div class="grid gap-6 grid-cols-1 my-8">
        <div>
          <Input
            v-model="formData.password"
            :view="(validation.password.$invalid && 'danger') || 'default'"
            placeholder="Password"
            autocomplete="new-password"
            :type="isShowPassword ? 'text' : 'password'"
            id="password"
            block
            icon
            right
          >
            <template #icon>
              <span
                class="material-icons block cursor-pointer text-grey-03"
                @click="isShowPassword = !isShowPassword"
                >{{ isShowPassword ? 'visibility_off' : 'visibility' }}</span
              >
            </template>
          </Input>
          <span class="text-grey-03 block my-2 text-sm"
            >Use a minimum of 6 characters [case sensitive] with at least one number or special character</span
          >
          <span
            class="text-red-base mt-2 block"
            v-for="(error, errorIndex) of validation.password.$errors"
            :key="errorIndex"
            >{{ error.$message }}.
          </span>
        </div>
        <div>
          <Input
            v-model="formData.confirm_password"
            :view="
              (validation.confirm_password.$invalid && 'danger') || 'default'
            "
            placeholder="Confirm Password"
            autocomplete="new-password"
            :type="isShowConfirmPassword ? 'text' : 'password'"
            id="confirmPassword"
            block
            icon
            right
          >
            <template #icon>
              <span
                class="material-icons block cursor-pointer text-grey-03"
                @click="isShowConfirmPassword = !isShowConfirmPassword"
                >{{
                  isShowConfirmPassword ? 'visibility_off' : 'visibility'
                }}</span
              >
            </template>
          </Input>
          <span
            class="text-red-base mt-2 block"
            v-for="(error, errorIndex) of validation.confirm_password.$errors"
            :key="errorIndex"
            >{{ error.$message }}.
          </span>
        </div>
      </div>
    </div>
    <div>
      <p whitelabel="remove" class="text-grey-02 text-center mb-8 mt-2" style="font-size: 0.9rem">
        By clicking "Continue" you agree to reusely.com's
        <a
          href="https://www.reusely.com/terms-of-service"
          rel="noreferrer noopener"
          target="_blank"
          >Terms and Conditions</a
        >
        and
        <a
          href="https://www.reusely.com/privacy-policy"
          rel="noreferrer noopener"
          target="_blank"
          >Privacy Policy</a
        >
      </p>
    </div>
    <div class="flex items-center">
      <div
        class="flex items-center font-bold cursor-pointer px-4 py-4 mr-8 select-none"
        @click="$router.push({ name: 'login' })"
      >
        <i class="material-icons icon-sm font-bold mr-4">chevron_left</i>
        <span class="text-lg">Login</span>
      </div>
      <Button
        class="flex-grow"
        color="teal"
        @click="handleSubmit"
        :disabled="!isReady"
        :loading="loading.all"
        >Continue</Button
      >
    </div>
  </div>
</template>

<script>
import { reactive, defineComponent, ref, watch, computed } from 'vue'
import { useVuelidate } from '@vuelidate/core'
import { email, helpers } from '@vuelidate/validators'
import { required } from '@/validations/index'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import Select from 'components/Select.vue'
import InputPhone from 'components/InputPhone.vue'
import SelectCountry from 'components/SelectCountry.vue'
import SelectCurrency from 'components/SelectCurrency.vue'

export default defineComponent({
  name: 'RegisterUserInfo',
  components: {
    Input,
    Button,
    Select,
    InputPhone,
    SelectCountry,
    SelectCurrency
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        first_name: null,
        last_name: null,
        email: null,
        password: null,
        confirm_password: null,
        country_code: null,
        country_name: null,
        currency: null,
        currency_symbol: null,
        phone_number: null,
        location_type: null
      })
    },
    countries: {
      type: Array,
      default: () => []
    },
    currencies: {
      type: Array,
      default: () => []
    },
    loading: {
      type: Object,
      default: () => ({
        currencies: false,
        countries: false
      })
    }
  },
  emits: ['change', 'submit'],
  setup(props, { emit }) {
    const isShowPassword = ref(false)
    const isShowConfirmPassword = ref(false)
    const formData = reactive({ ...props.data })
    const rules = {
      first_name: {
        required: required
      },
      last_name: {
        required: required
      },
      email: {
        required: required,
        email: email
      },
      location_type: {
        required
      },
      password: {
        required: required,
        passwordValidation: helpers.withMessage(
          'At least 6 characters and include capital letters',
          helpers.regex(/^(?=.*?[A-Z]).{6,}$/)
        )
      },
      confirm_password: {
        required: required,
        confirmPassword: helpers.withMessage(
          'Passwords do not match',
          () => formData.password === formData.confirm_password
        )
      },
      country_code: {
        required: required
      },
      currency: {
        required: required
      },
      phone_number: {
        required: required
      }
    }
    const validation = useVuelidate(rules, formData, {
      $lazy: true
    })

    const isReady = computed(() => {
      for (let [_, value] of Object.entries(formData)) {
        if (!value) return false
      }
      return true
    })
    const locationTypes = computed(() => [
      {
        name: 'In-Store',
        value: 2
      },
      {
        name: 'Mail-In',
        value: 1
      }
    ])

    watch(
      () => props.data,
      (newData) => {
        Object.assign(formData, {
          ...formData,
          ...newData
        })
      },
      {
        deep: true
      }
    )
    watch(
      () => formData,
      (newData) => {
        emit('change', newData)
      },
      {
        deep: true
      }
    )

    const handleChangeCountry = (newCountry) => {
      formData.country_code = newCountry.country_code
      formData.country_name = newCountry.nicename
    }
    const handleChangeCurrency = (newCurrency) => {
      formData.currency = newCurrency.currency_code
      formData.currency_symbol = newCurrency.symbol
    }
    const handleSubmit = () => {
      emit('submit', validate())
    }
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }

    return {
      validation,
      formData,
      isShowPassword,
      isShowConfirmPassword,
      isReady,
      locationTypes,
      handleChangeCountry,
      handleChangeCurrency,
      handleSubmit,
      validate
    }
  }
})
</script>
<style lang="scss" scoped></style>
