<template>
	<div v-loading="isLoadingAffiliate">
		<template v-if="renderedComponent === 'Plan'">
			<component :is="renderedComponent" v-bind="componentBind"></component>
		</template>
		<template v-else>
			<div class="flex flex-row flex-wrap justify-center px-6">
				<div class="register-card xl:max-w-6xl lg:max-w-6xl max-w-full">
					<div class="register-card__header">
						<img
							class="h-16 mx-auto mb-4"
							src="@/assets/logo.svg"
							alt="refreshedtech.com logo"
							whitelabel="image-replace"
							data-image-type="logo" />
						<div v-if="currentStepIndex !== 4" class="register-nav">
							<div v-for="(nav, navIndex) of steps" :key="navIndex">
								<div
                  v-if="!nav.is_hidden"
                  v-html="nav.label"
									:class="[
										'register-nav__item',
										currentStepIndex === navIndex && '--active'
									]"></div>
							</div>
						</div>
					</div>
					<div class="register-card__content">
						<component
							:is="renderedComponent"
							v-bind="componentBind"></component>
					</div>
				</div>
			</div>
		</template>
	</div>
</template>

<script>
import { computed, defineComponent, onMounted, ref } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useRegister, useVerify, useCheckout } from 'composable/user'
import Plan from './Plan.vue'
import UserInfo from './UserInfo.vue'
import ActivationCode from './ActivationCode.vue'
import Checkout from './Checkout.vue'

export default defineComponent({
	name: 'Register',
	components: {
		Plan,
		UserInfo,
		ActivationCode,
		Checkout
	},
	setup() {
		const {
			data: userData,
			currencies,
			countries,
			loading,
			action,
			actionCurrencies,
			actionCountries
		} = useRegister()
		const {
			data: verifyData,
			loading: loadingVerify,
			action: actionVerify
		} = useVerify()
		const {
			// action: actionCheckout,
			// actionRemoveLocation,
			checkoutStatus
		} = useCheckout()
		const route = useRoute()
		const router = useRouter()
		const currentStep = ref(1)
		const sessionId = ref(null)
		const isCheckout = ref(false)
		const isCheckoutSuccess = ref(false)
		const isLoadingAffiliate = ref(false)
		const verifyToken = ref(null)
		const verifyEmail = ref(null)

		if (route.name === 'registerCheckout') {
      currentStep.value = 5
      sessionId.value = route.query?.session_id || ''
      isCheckout.value = true
    }

		const steps = ref([
			{
				label: 'Choose <br/> Package',
				component_name: 'Plan',
				is_hidden: true
			},
			{
				label: 'Enter <br/> your info',
				component_name: 'UserInfo',
				is_hidden: false
			},
			{
				label: 'Activation <br/> Code',
				component_name: 'ActivationCode',
				is_hidden: false  
			},
			{
				label: 'Payment <br/> Information',
				component_name: null,
				is_hidden: false
			},
			{
				label: 'Start your <br/> Business now',
				component_name: 'Checkout',
				is_hidden: false
			}
		])
		const currentStepIndex = computed(() => currentStep.value - 1)
		const renderedComponent = computed(() => {
			const currentStepIndex = currentStep.value - 1
			const isValidIndex =
				currentStepIndex > -1 &&
				typeof steps.value[currentStepIndex] !== 'undefined'
			if (isValidIndex) {
				return steps.value[currentStepIndex].component_name
			}
			return 'Plan'
		})
		const componentBind = computed(() => {
			if (currentStep.value === 1)
				return {
					onChange: handlePlanUpdate
				}
			if (currentStep.value === 2)
				return {
					onChange: handleUserDataUpdate,
					onSubmit: handleUserDataSubmit,
					countries: countries.value,
					currencies: currencies.value,
					loading: loading
				}
			if (currentStep.value === 3)
				return {
					onChange: handleVerifyDataUpdate,
					onSubmit: handleVerifyDataSubmit,
					email: userData.email || verifyEmail.value,
					loading: loadingVerify.value
				}
			if (currentStep.value === 5)
				return {
					success: isCheckoutSuccess.value
				}
			return {}
		})
		const handlePlanUpdate = (packageId) => {
			if (!!packageId) {
				userData.package_id = packageId
				currentStep.value++
			}
		}
		const handleUserDataUpdate = (newUserData) => {
			Object.assign(userData, {
				...userData,
				...newUserData
			})
		}
		const handleVerifyDataUpdate = (newVerifyData) => {
			Object.assign(verifyData, {
				...verifyData,
				...newVerifyData
			})
		}
		const handleUserDataSubmit = async (isValid) => {
			if (isValid) {
				await action((response) => {
					verifyData.company_token = response.token
					router.push({
						name: 'registerVerify',
						query: {
							t: response.token,
							email: userData.email
						}
					})
					currentStep.value++
				})
			}
		}
		const handleVerifyDataSubmit = async (isValid) => {
			if (isValid) {
				actionVerify((response) => {
					const checkoutUrl = response?.url
					if (!!checkoutUrl) window.location.replace(checkoutUrl)
				})
			}
		}

		onMounted(async () => {
			if (typeof LinkMink==="function") {
				isLoadingAffiliate.value = true
				const lm = LinkMink(import.meta.env.VITE_LINKMINK_PUB)
				await lm.initTracking()
				const lmData = lm.getReferralData()
				if(lmData) userData.referral = lmData
				isLoadingAffiliate.value = false
			}

			if (route.name === 'registerVerify') {
				verifyToken.value = route.query['t']
				verifyEmail.value = route.query['email']

				const isInvalidVerify = !verifyToken.value || !verifyEmail.value
				if (isInvalidVerify) {
					router.push({
						name: 'register'
					})
				}

				verifyData.company_token = verifyToken.value
				currentStep.value = 3
			}

			if (isCheckout.value) {
				isCheckoutSuccess.value = checkoutStatus.value
				// try {
				// 	await actionCheckout(sessionId.value, async (response) => {
				// 		const token = response?.token || ''
				// 		window.localStorage.setItem('token', token)
				// 		isCheckoutSuccess.value = true
				// 		await actionRemoveLocation()
				// 	})
				// } catch (e) {}
			}
			
			// const hasDefinedPlanName = !!route.query.plan
			// if(hasDefinedPlanName && route.name === 'register') {
			// 	switch(route.query.plan.toLowerCase()){
			// 		case 'copper':
			// 			userData.package_id = 12
			// 			break
			// 		case 'silver':
			// 			userData.package_id = 13
			// 			break
			// 		default:
			// 			userData.package_id = 12
			// 	}
			// 	currentStep.value = 2
			// }

			await Promise.all([actionCurrencies(), actionCountries()])
		})
		return {
			isLoadingAffiliate,
			steps,
			currentStep,
			currentStepIndex,
			renderedComponent,
			componentBind,
			userData,
			verifyData
		}
	}
})
</script>
<style lang="scss" scoped>
.register-card {
	@apply px-12 py-8 bg-white rounded-xl w-max my-20 shadow-2xl;
	&__header {
		@apply my-2;
	}
	&__content {
		@apply my-2;
	}
}
.register-nav {
	@apply flex items-stretch flex-wrap justify-center my-8;
	&__item {
		@apply flex flex-grow items-center text-center flex-wrap px-12 py-4 bg-grey-lighten text-grey-03 select-none transition duration-300 font-semibold;
		&:first-child {
			@apply rounded-l-lg;
		}
		&:last-child {
			@apply rounded-r-lg;
		}
		&.--active {
			@apply bg-teal-01 text-teal-04 cursor-default;
		}
	}
}
</style>
