<template>
  <div class="w-full">
    <img
      class="mb-10 mx-auto"
      src="@/assets/icon/mail-verification.svg"
      alt="Verify Icon"
    />
    <div class="text-center">
      <h5 class="text-black mb-2">
        Please check your email for your activation code
      </h5>
      <p class="text-black">
        You will receive an email with an activation code with-in the next few
        minutes to <span class="block font-semibold mt-4">{{ email }}</span
        >.
      </p>
    </div>
    <form @submit.prevent="submit">
      <div class="block mb-10">
        <div class="grid grid-cols-7 gap-4">
          <Input
            v-model="dataInput[0]"
            maxlength="1"
            class="text-4xl text-center"
            :view="(validation.code.$invalid && 'danger') || 'default'"
            @input="handleInput"
            @keydown="handleKeydown"
            @paste="handlePaste"
          />
          <Input
            v-model="dataInput[1]"
            maxlength="1"
            class="text-4xl"
            :view="(validation.code.$invalid && 'danger') || 'default'"
            @input="handleInput"
            @keydown="handleKeydown"
          />
          <Input
            v-model="dataInput[2]"
            maxlength="1"
            class="text-4xl"
            :view="(validation.code.$invalid && 'danger') || 'default'"
            @input="handleInput"
            @keydown="handleKeydown"
          />
          <div class="flex flex-column items-center justify-center">
            <span class="material-icons align-middle">horizontal_rule</span>
          </div>
          <Input
            v-model="dataInput[3]"
            maxlength="1"
            class="text-4xl"
            :view="(validation.code.$invalid && 'danger') || 'default'"
            @input="handleInput"
            @keydown="handleKeydown"
          />
          <Input
            v-model="dataInput[4]"
            maxlength="1"
            class="text-4xl"
            :view="(validation.code.$invalid && 'danger') || 'default'"
            @input="handleInput"
            @keydown="handleKeydown"
          />
          <Input
            v-model="dataInput[5]"
            maxlength="1"
            class="text-4xl"
            :view="(validation.code.$invalid && 'danger') || 'default'"
            @input="handleInput"
            @keydown="handleKeydown"
          />
        </div>
        <span
          class="text-red-base mt-2 block"
          v-for="(error, errorIndex) of validation.code.$errors"
          :key="errorIndex"
          >{{ error.$message }}. </span
        >
      </div>
      <div class="block my-5">
        <Button
          view="filled"
          color="teal"
          type="submit"
          :loading="loading"
          block
          >Continue</Button
        >
      </div>
    </form>
  </div>
</template>

<script>
import { ref, defineComponent, watch, reactive } from 'vue'
import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import Input from 'bbUi/components/input/input.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import { useVuelidate } from '@vuelidate/core'
import { required, minLength } from '@/validations/index'

export default defineComponent({
  name: 'RegisterActivationCode',
  components: {
    Button,
    Card,
    Input,
    Checkbox
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        code: null
      })
    },
    loading: {
      type: Boolean,
      default: false
    },
    email: {
      type: String,
      default: ''
    }
  },
  emits: ['change', 'submit'],
  setup(props, { emit }) {
    // const { data, loading, action } = useVerify()
    const formData = reactive({ ...props.data })
    const submit = () => {
      emit('submit', validate())
    }
    const dataInput = ref(['', '', '', '', '', ''])
    const rules = {
      code: { required, minLength: minLength(6) }
    }
    const validation = useVuelidate(rules, formData, {
      $lazy: true
    })
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }
    const handlePaste = (e) => {
      const paste = e.clipboardData.getData('text')
      dataInput.value.forEach((input, i) => {
        dataInput.value[i] = paste[i] || ''
      })
    }
    const handleInput = (e) => {
      const input = e.path[2]
      if (
        input.nextElementSibling &&
        input.nextElementSibling.classList.contains('input') &&
        input.children[0].children[0].value
      ) {
        input.nextElementSibling.children[0].children[0].focus()
      } else if (
        input.nextElementSibling &&
        !input.nextElementSibling.classList.contains('input') &&
        input.children[0].children[0].value
      ) {
        input.nextElementSibling.nextElementSibling.children[0].children[0].focus()
      }
    }
    const handleKeydown = (e) => {
      if (e.keyCode === 8) {
        const input = e.path[2]
        if (
          input.previousElementSibling &&
          input.previousElementSibling.classList.contains('input') &&
          !input.children[0].children[0].value
        ) {
          input.previousElementSibling.children[0].children[0].focus()
        } else if (
          input.previousElementSibling &&
          !input.previousElementSibling.classList.contains('input') &&
          !input.children[0].children[0].value
        ) {
          input.previousElementSibling.previousElementSibling.children[0].children[0].focus()
        }
      }
    }

    watch(
      dataInput,
      (newValue) => {
        formData.code = newValue.join('')
        emit('change', formData)
      },
      {
        deep: true
      }
    )

    watch(
      () => props.data,
      (newData) => {
        Object.assign(formData, {
          ...formData,
          ...newData
        })
      }
    )

    return {
      dataInput,
      submit,
      validation,
      handlePaste,
      handleInput,
      handleKeydown
    }
  }
})
</script>
